* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
}


@mixin arrow($color,$size) {
  position: relative;
  &:after {
    content: '';
    display: block;
    height: 0;
    width: 0;
    border: $size solid transparent;
    border-bottom-color: $color;
    position: absolute;
    left: 50%;
    top: -($size*2);
    transform: translateX(-50%);
  }
}

a {
  text-decoration: none;
}

label {
  display: none;
}

.site-header {
  display: flex;
  background-color: black;
  height: 60px;
  width: 100vw;
  color: white;
  .logo {
    text-transform: uppercase;
    font-weight: bold;
    span {
      color: #23C8AA;
    }
  }
}

.container {
  width: 1000px;
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding-left: 50px;
  padding-right: 50px;

}

.row {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: space-between;
}

.logo {
