  }

}
.prod-img{

  height: 225px;

  overflow: hidden;
  cursor: pointer;
  img{
    transition: 1.5s all ;
  }
  .name{
    transition: 0.3s all
  }
}
.prod-img:hover{
  img{
    transform:scale(1.15);
  }
  .name{
    transform: scale(0);
  }
}

